import axios from 'axios';
import * as actionType from './dataUserType';
import API from '../../root/';
import { history } from '../../helpers/history';
import swal from 'sweetalert';


// fetch all data
export function fetchData(data){
    return{
        type: actionType.GET_DATA_USER_SUCCESS,
        payload: { data }
    }
}

export function failedFetchData(error){
    return{
        type: actionType.GET_DATA_USER_FAILED,
        payload: { error }
    }
}

// fetch data by id
export function fetchDataById(datas){
    return{
        type: actionType.GET_DATA_USER_BY_ID_SUCCESS,
        payload: { datas }
    }
}

export function failedFetchDataById(error){
    return{
        type: actionType.GET_DATA_USER_BY_ID_FAILED,
        payload: { error }
    }
}

// post data
export function failedPostData(error){
    return{
        type: actionType.POST_DATA_USER_FAILED,
        payload: { error }
    }
}

// delete data
export function failedDeleteData(error){
    return{
        type: actionType.DELETE_DATA_USER_FAILED,
        payload: { error }
    }
}

// edit data
export function successEditData(data){
    return{
        type: actionType.EDIT_DATA_USER_SUCCESS,
        data
    }
}
export function failedEditData(error){
    return{
        type: actionType.EDIT_DATA_USER_FAILED,
        payload: { error }
    }
}

export function getData(){
    return function(dispatch){
        dispatch({ type: actionType.GET_DATA_USER_REQUEST, loading: true })
        const URL = API+`get/user/`
        return axios.get(URL)
            .then((response) => {
                dispatch(fetchData(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchData(error))
            })
    }
}

export function getDataById(id){
    return function(dispatch){
        dispatch({ type: actionType.GET_DATA_USER_BY_ID_REQUEST, loading: true })
        const URL = API+`get/user/${id}`
        return axios.get(URL)
            .then((response) => { 
                dispatch(fetchDataById(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchDataById(error))
            })
    }
}

export function createData(data){
    return function(dispatch){
        dispatch({ type: actionType.POST_DATA_USER_REQUEST, loading: true })
        const URL = API+`post/user`
        return axios.post(URL, data)
            .then((response) => {
                dispatch({ type: actionType.POST_DATA_USER_SUCCESS, payload: response.data })
                swal({
                    icon: 'success',
                    title: 'Berhasil Tambah Data',
                    button: true
                })
                history.push('/data-user')
            })
            .catch((error) => {
                dispatch(failedPostData(error))
            })
    }
}

export function deleteData(id){
    return function(dispatch){
        dispatch({ type: actionType.DELETE_DATA_USER_REQUEST, loading: true })
        const URL = API+`delete/user/${id}`
        return axios.delete(URL)
            .then((response) => {
                dispatch({ type: actionType.DELETE_DATA_USER_SUCCESS, success: true })
                dispatch(getData())
            })
            .catch((error) => {
                dispatch(failedDeleteData(error))
            })
    }
}

export function editData(data){
    return function(dispatch){
        dispatch({ type: actionType.EDIT_DATA_USER_REQUEST, loading: true })
        const URL = API+`edit/user`
        return axios.post(URL, data)
            .then((response) => {
                dispatch({ type: actionType.EDIT_DATA_USER_SUCCESS, payload: response.data })
                swal({
                    icon: 'success',
                    title: 'Berhasil Edit Data',
                    button: true
                })
                history.push('/data-user')
            })
            .catch((error) => {
                dispatch(failedEditData(error))
            })
    }
}