import React, { Component } from 'react';
import { 
  Button,
  Card, 
  CardBody, 
  CardHeader, 
  Col,
  Collapse,
  Row,
  Fade, 
  FormGroup,
} from 'reactstrap';
import { Link, withRouter } from 'react-router-dom';
import swal from 'sweetalert';
import { connect } from 'react-redux';
import { getData, deleteData } from '../../redux/dataUser/dataUserAction';
import ReactTable from 'react-table';
import "react-table/react-table.css";

class Tables extends Component {
  constructor(props){
    super(props);
    this.state = { 
        modal: false,
        collapse: true,
        fadeIn: true,
        timeout: 300,
        data: []
    }
    this.toggle = this.toggle.bind(this);
    this.toggling = this.toggling.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
  }
  
  toggle(){
    this.setState({ modal: !this.state.modal });
  };

  toggling(){
    this.setState({ collapse: !this.state.collapse });
  };

  toggleFade(){
    this.setState((prevState) => { return { fadeIn: !prevState } });
  };

  handleDelete(event, id){
    event.preventDefault();
    swal({
      title: 'Apakah Anda Yakin?',
      text: 'Ingin Menghapus Data Ini?',
      icon: 'warning',
      buttons: true,
      dangerMode: true
    })
    .then((willDelete) => {
      if(willDelete){
        swal(
          'Deleted!', "Data Berhasil Dihapus",
          { icon: "success" }
        )
        .then(
          this.props.dispatch(deleteData(id)),
        )
      }else{
        swal('Data Tidak Jadi Dihapus')
      }
    });
  }

  async componentWillMount(){
    await this.props.dispatch(getData())
  }

  async componentWillReceiveProps(newProps){
    this.setState({
      data: await newProps.data.data.data
    })
  }

  // async componentDidMount(){
  //   await this.props.dispatch(getData());  
  // }

  render() {
    // const { data } = this.props.data;
    // console.log(this.state.data)
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
              <Card>
                <CardHeader>
                  <i className="fa fa-align-justify"></i> Data User
                  <div className="card-header-actions">
                    <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                    <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggling}><i className="icon-arrow-up"></i></Button>
                  </div>
                </CardHeader>
                <Collapse isOpen={this.state.collapse} id="collapseExample">
                  <CardBody>
                    <Row>
                      <Col xs="12" sm="12">
                        <FormGroup row>
                          <Col xs="12" md="5">
                            <Link to="/data-user/tambah-data">
                                <Button color="primary" size="md">  
                                    <i className="icon-plus"> Tambah </i> 
                                </Button>
                            </Link> 
                          </Col>
                        </FormGroup>
                      </Col>
                    </Row>
                    <ReactTable
                      data={this.state.data}
                      filterable
                      sortable
                      columns={[
                        {
                          Header: "Nama",
                          accessor: "nama",
                          width: 150
                        },
                        {
                          Header: "Email",
                          accessor: "email",
                          width: 250
                        },
                        {
                          Header: "Username",
                          accessor: "username",
                          width: 100
                        },
                        {
                          accessor: "actions",
                          sortable: false,
                          filterable: false,
                          width: 80,
                          Cell: props =>
                            <div>
                              <Link to={"/data-user/edit-data/"+props.original.id}>
                                <Button color="primary" size="sm"> 
                                  <i className="icon-pencil"> </i> 
                                </Button>
                              </Link>
                              &nbsp;&nbsp;
                              <Button color="danger" size="sm" onClick={(event) => { this.handleDelete(event, props.original.id) }}> 
                                <i className="icon-trash"> </i> 
                              </Button>
                            </div>
                        }
                      ]}
                      defaultPageSize={5}
                      showPaginationBottom={true}
                      className="-striped -highlight"
                    />
                  </CardBody>
                </Collapse>
              </Card>
            </Fade>
          </Col>
        </Row>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
    return{
    data: state.dataUser,
  }
};

export default withRouter(connect(mapStateToProps)(Tables));